import React, { useEffect, useMemo, useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogDescription } from '@/components/ui/dialog';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { Progress } from '@/components/ui/progress';
import { Receipt, Calendar, Clock, ChevronDown, Trash2, RefreshCcw, PlayCircle } from 'lucide-react';
import FawryMachineIcon from '@/components/icons/FawryMachineIcon';
import { useToast } from '@/hooks/use-toast';
import { useShift } from '@/contexts/ShiftContext';
import { AlertDialog, AlertDialogContent, AlertDialogHeader, AlertDialogTitle, AlertDialogDescription, AlertDialogAction, AlertDialogCancel } from '@/components/ui/alert-dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Checkbox } from '@/components/ui/checkbox';
import { db } from '@/firebase';
import { addDoc, collection, doc, setDoc, serverTimestamp, deleteDoc, getDocs, orderBy, query } from 'firebase/firestore';
import { useShop } from '@/hooks/useShop';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { userDataService } from '@/integrations/firebase/services';
import { ProfitService } from '@/utils/profitService';
import { VideoModal } from '@/components/VideoModal';

interface MachineDailyDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

interface MachineReceipt {
  id: string;
  openingBase: number; // الرصيد الأساسي الافتتاحي
  openingAir: number;  // رصيد الهواء الافتتاحي
  deliveryBase: number; // الرصيد الأساسي عند التسليم
  deliveryAir: number;  // رصيد الهواء عند التسليم
  netResult: number;    // الناتج النهائي: (تسليم) - (افتتاحي)
  createdAt: string;    // التاريخ والوقت
  cashierName?: string; // اسم الكاشير إن وجد
  requiredDrawerNoProfit?: number; // المطلوب من الدرج بدون أرباح
}

interface TransferReceipt {
  id: string;
  wholesalePrice: number; // سعر الجملة للرصيد المحوّل
  retailPrice: number;    // سعر التجزئة للرصيد المحوّل
  profit: number;         // الربح الفوري: التجزئة - الجملة
  createdAt: string;
  cashierName?: string;
  deductFrom?: 'base' | 'air'; // مصدر الخصم: الأساسي أو الهواء
}

const formatNumber = (n: number) => {
  try { return Math.round(Number(n || 0)).toLocaleString('en-US', { maximumFractionDigits: 0 }); } catch { return String(n); }
};

const formatDateTime = (iso?: string) => {
  try {
    const d = iso ? new Date(iso) : new Date();
    return d.toLocaleString('en-GB', {
      year: 'numeric', month: '2-digit', day: '2-digit',
      hour: '2-digit', minute: '2-digit', second: '2-digit',
      hour12: false,
    });
  } catch {
    return new Date().toISOString();
  }
};

// مفاتيح التخزين المحلي للحفاظ على القيم أثناء فتح النافذة
const STORAGE_KEY_OPENING = 'machine_daily_opening_values';
const STORAGE_KEY_OPENING_SNAPSHOT = 'machine_daily_opening_snapshot';

export default function MachineDailyDialog({ open, onOpenChange }: MachineDailyDialogProps) {
  const { toast } = useToast();
  const { shiftUser } = useShift();
  const { user, profile } = useFirebaseAuth();
  const { shopId, shopName } = useShop();
  const getFallbackShopId = () => {
    try {
      if (user?.uid) {
        const key = `selectedShopId_${user.uid}`;
        const val = localStorage.getItem(key);
        if (val) return val;
      }
      const keys = Object.keys(localStorage || {}).filter(k => k.startsWith('selectedShopId_'));
      for (const k of keys) {
        const v = localStorage.getItem(k);
        if (v) return v;
      }
      return null;
    } catch {
      return null;
    }
  };

  // منع تمرير الصفحة عند فتح نافذة اليومية وإعادة الحالة عند الإغلاق
  useEffect(() => {
    try {
      if (open) {
        const prev = document.body.style.overflow;
        document.body.setAttribute('data-prev-overflow', prev);
        document.body.style.overflow = 'hidden';
      } else {
        const prev = document.body.getAttribute('data-prev-overflow') || '';
        document.body.style.overflow = prev;
        document.body.removeAttribute('data-prev-overflow');
      }
    } catch {}
    return () => {
      try {
        const prev = document.body.getAttribute('data-prev-overflow') || '';
        document.body.style.overflow = prev;
        document.body.removeAttribute('data-prev-overflow');
      } catch {}
    };
  }, [open]);

  // إدخالات الرصيد الافتتاحي
  const [openingBase, setOpeningBase] = useState<string>('');
  const [openingAir, setOpeningAir] = useState<string>('');
  const [drawerCash, setDrawerCash] = useState<string>('');

  // القيم المسجلة للرصيد الافتتاحي
  const [savedOpeningBase, setSavedOpeningBase] = useState<number | null>(null);
  const [savedOpeningAir, setSavedOpeningAir] = useState<number | null>(null);
  const [savedDrawerCash, setSavedDrawerCash] = useState<number | null>(null);
  const [openingSnapshot, setOpeningSnapshot] = useState<{ openingBase: number; openingAir: number; drawerCash: number } | null>(null);

  const [activeSection, setActiveSection] = useState<'opening' | 'transfer' | 'transferReceipts' | 'delivery' | 'deliveryReceipts' | 'machines' | 'cashWithdrawal' | 'withdrawalReceipts'>('opening');

  // قسم التسليم اليومي
  const [deliveryBase, setDeliveryBase] = useState<string>('');
  const [deliveryAir, setDeliveryAir] = useState<string>('');
  const [netResult, setNetResult] = useState<number | null>(null);

  // إيصالات داخل النافذة (بدون تخزين محلي)
  const [receipts, setReceipts] = useState<MachineReceipt[]>([]);
  
  // إيصالات سحب الكاش
  const [withdrawalReceipts, setWithdrawalReceipts] = useState<any[]>([]);

  // إيصالات تحويل الرصيد (بدون تخزين محلي)
  const [transferReceipts, setTransferReceipts] = useState<TransferReceipt[]>([]);

  // حالة نافذة التحويل وأسعار الجملة/التجزئة
  const [transferDialogOpen, setTransferDialogOpen] = useState(false);
  const [isVideoModalOpen, setIsVideoModalOpen] = useState(false);
  const [wholesalePrice, setWholesalePrice] = useState<string>('');
  const [retailPrice, setRetailPrice] = useState<string>('');

  // نافذة اختيار الخصم بعد إدخال الأسعار
  const [deductDialogOpen, setDeductDialogOpen] = useState(false);
  const [pendingTransfer, setPendingTransfer] = useState<{
    wholesalePrice: number;
    retailPrice: number;
    profit: number;
  } | null>(null);

  // إدارة الماكينات (اختيار/إنشاء)
  const [machines, setMachines] = useState<{ id: string; name: string; monthlyLimit?: number }[]>([]);
  const [selectedMachineId, setSelectedMachineId] = useState<string | null>(null);
  const [newMachineName, setNewMachineName] = useState<string>('');
  const [transferServerHydrated, setTransferServerHydrated] = useState(false);
  const [deliveriesServerHydrated, setDeliveriesServerHydrated] = useState(false);

  // Cash Withdrawal State
  const [withdrawalAmount, setWithdrawalAmount] = useState<string>('');
  const [commissionMode, setCommissionMode] = useState<'rate' | 'fixed'>('rate');
  const [commissionRate, setCommissionRate] = useState<string>('2'); // Default 2 per 1000
  const [fixedCommissionAmount, setFixedCommissionAmount] = useState<string>('');
  const [monthlyWithdrawalsUsed, setMonthlyWithdrawalsUsed] = useState<number>(0);
  const [dailyWithdrawalsUsed, setDailyWithdrawalsUsed] = useState<number>(0);
  const [monthlyWithdrawalsProfit, setMonthlyWithdrawalsProfit] = useState<number>(0);
  const [dailyWithdrawalsProfit, setDailyWithdrawalsProfit] = useState<number>(0);
  const [deductFromDrawer, setDeductFromDrawer] = useState<boolean>(false);
  const [isProcessing, setIsProcessing] = useState(false);
  const isProcessingRef = React.useRef(false);

  // لتجنب الكتابة فوق البيانات الجديدة إذا تأخر السيرفر
  const latestTransferId = React.useRef<string | null>(null);
  const latestDeliveryId = React.useRef<string | null>(null);

  // Reset processing state when dialog opens
  useEffect(() => {
    if (open) {
      isProcessingRef.current = false;
      setIsProcessing(false);
    }
  }, [open]);

  // Hydrate per-machine opening values when dialog opens or machine changes
  useEffect(() => {
    if (!open) return;
    try {
      const keyOpening = selectedMachineId ? `${STORAGE_KEY_OPENING}_${selectedMachineId}` : STORAGE_KEY_OPENING;
      const keySnap = selectedMachineId ? `${STORAGE_KEY_OPENING_SNAPSHOT}_${selectedMachineId}` : STORAGE_KEY_OPENING_SNAPSHOT;
      const savedOpening = localStorage.getItem(keyOpening);
      if (savedOpening) {
        const obj = JSON.parse(savedOpening);
        if (typeof obj?.openingBase === 'number') setSavedOpeningBase(obj.openingBase);
        if (typeof obj?.openingAir === 'number') setSavedOpeningAir(obj.openingAir);
        if (typeof obj?.drawerCash === 'number') setSavedDrawerCash(obj.drawerCash);
      } else {
        setSavedOpeningBase(null);
        setSavedOpeningAir(null);
        setSavedDrawerCash(null);
      }

      const savedSnap = localStorage.getItem(keySnap);
      if (savedSnap) {
        const snap = JSON.parse(savedSnap);
        if (typeof snap?.openingBase === 'number' && typeof snap?.openingAir === 'number') {
          setOpeningSnapshot({ openingBase: snap.openingBase, openingAir: snap.openingAir, drawerCash: snap.drawerCash || 0 });
        } else {
          setOpeningSnapshot(null);
        }
      } else {
        setOpeningSnapshot(null);
      }
      
      // Clear inputs on machine change
      setOpeningBase('');
      setOpeningAir('');
      setDrawerCash('');
    } catch {}
  }, [open, selectedMachineId]);

  // تحميل قائمة الماكينات عند فتح النافذة
  useEffect(() => {
    (async () => {
      try {
        const sid = shopId || getFallbackShopId();
        if (!open || !sid) return;
        const { getDocs } = await import('firebase/firestore');
        const col = collection(db, 'shops', sid, 'machines');
        const snap = await getDocs(query(col));
        const list = snap.docs.map(d => ({ 
          id: d.id, 
          name: String(d.data().name || 'ماكينة'),
          monthlyLimit: Number(d.data().monthlyLimit || 50000) 
        }));
        setMachines(list);
        try {
          const savedId = localStorage.getItem(`machineDaily_selected_${sid}`);
          const exists = savedId && list.some(m => m.id === savedId);
          if (exists) {
            setSelectedMachineId(savedId as string);
          } else if (!selectedMachineId && list.length > 0) {
            setSelectedMachineId(list[0].id);
            localStorage.setItem(`machineDaily_selected_${sid}`, list[0].id);
          }
        } catch {}
      } catch (err) {
        console.error('Load machines error:', err);
      }
    })();
  }, [open, shopId]);

  // تم إلغاء مزامنة الإيصالات إلى التخزين المحلي

  const cashierName = useMemo(() => {
    if (shiftUser) {
      return (shiftUser as any).displayName || shiftUser.name || shiftUser.username || 'كاشير';
    }
    return (profile as any)?.fullName || user?.displayName || 'الحساب الرئيسي';
  }, [shiftUser, profile, user]);

  const totalTransferProfit = useMemo(() => transferReceipts.reduce((sum, t) => sum + (t.profit || 0), 0), [transferReceipts]);
  const totalWholesaleTransferred = useMemo(() => transferReceipts.reduce((sum, t) => sum + (t.wholesalePrice || 0), 0), [transferReceipts]);

  const handleClearTransferReceipts = async () => {
    try {
      const sid = shopId || getFallbackShopId();
      if (sid && selectedMachineId) {
        const col = collection(db, 'shops', sid, 'machines', selectedMachineId, 'transfers');
        const snap = await getDocs(query(col));
        await Promise.all(snap.docs.map(d => deleteDoc(d.ref)));
        try { localStorage.setItem(`machineDaily_transfers_${sid}_${selectedMachineId}`, JSON.stringify([])); } catch {}
      }
    } catch {}
    setTransferReceipts([]);
    toast({ title: 'تم حذف الإيصالات', description: 'تم مسح كل إيصالات التحويل.' });
  };

  const handleClearMachineReceipts = async () => {
    try {
      const sid = shopId || getFallbackShopId();
      if (sid && selectedMachineId) {
        const col = collection(db, 'shops', sid, 'machines', selectedMachineId, 'deliveries');
        const snap = await getDocs(query(col));
        await Promise.all(snap.docs.map(d => deleteDoc(d.ref)));
        try { localStorage.setItem(`machineDaily_deliveries_${sid}_${selectedMachineId}`, JSON.stringify([])); } catch {}
      }
    } catch {}
    setReceipts([]);
    toast({ title: 'تم حذف الإيصالات', description: 'تم مسح كل إيصالات الماكينة.' });
  };

  const handleDeleteTransferReceipt = async (receipt: TransferReceipt) => {
    if (!selectedMachineId) return;
    const sid = shopId || getFallbackShopId();
    if (!sid) return;

    // 1. Remove from state immediately (optimistic)
    const oldReceipts = [...transferReceipts];
    setTransferReceipts(prev => prev.filter(t => t.id !== receipt.id));

    try {
      // 2. Restore Balances
      const currentBase = savedOpeningBase || 0;
      const currentAir = savedOpeningAir || 0;
      const currentDrawer = savedDrawerCash || 0;

      let newBase = currentBase;
      let newAir = currentAir;
      let newDrawer = currentDrawer;

      // استرجاع الرصيد للمصدر (أساسي أو هواء)
      if (receipt.deductFrom === 'base') {
        newBase = Math.round((currentBase + receipt.wholesalePrice) * 100) / 100;
      } else if (receipt.deductFrom === 'air') {
        newAir = Math.round((currentAir + receipt.wholesalePrice) * 100) / 100;
      }
      
      // خصم المبلغ الذي تم إضافته للدرج (سعر التجزئة)
      newDrawer = Math.round((currentDrawer - receipt.retailPrice) * 100) / 100;

      // Update state
      setSavedOpeningBase(newBase);
      setSavedOpeningAir(newAir);
      setSavedDrawerCash(newDrawer);
      
      if (openingSnapshot) {
         setOpeningSnapshot({
            openingBase: newBase,
            openingAir: newAir,
            drawerCash: newDrawer
         });
      }

      // 3. Update Local Storage
      const keyOpening = `${STORAGE_KEY_OPENING}_${selectedMachineId}`;
      const keySnap = `${STORAGE_KEY_OPENING_SNAPSHOT}_${selectedMachineId}`;
      localStorage.setItem(keyOpening, JSON.stringify({ openingBase: newBase, openingAir: newAir, drawerCash: newDrawer }));
      localStorage.setItem(keySnap, JSON.stringify({ openingBase: newBase, openingAir: newAir, drawerCash: newDrawer }));
      
      const keyTrans = `machineDaily_transfers_${sid}_${selectedMachineId}`;
      const nextReceipts = oldReceipts.filter(t => t.id !== receipt.id);
      localStorage.setItem(keyTrans, JSON.stringify(nextReceipts));

      // 4. Update Firestore
      const receiptRef = doc(db, 'shops', sid, 'machines', selectedMachineId, 'transfers', receipt.id);
      await deleteDoc(receiptRef);

      const machineRef = doc(db, 'shops', sid, 'machines', selectedMachineId);
      await setDoc(machineRef, {
          openingBase: newBase,
          openingAir: newAir,
          drawerCash: newDrawer,
          updatedAt: serverTimestamp(),
      }, { merge: true });
      
      // 5. Revert User Transaction (Cash Balance)
      if (user?.uid) {
         const selectedMachineName = machines.find(m => m.id === selectedMachineId)?.name || 'ماكينة';
         
         await userDataService.saveUserTransaction(user.uid, {
            type: 'expense', 
            amount: receipt.retailPrice, 
            senderName: selectedMachineName,
            title: 'حذف إيصال تحويل',
            description: `إلغاء تحويل من ${selectedMachineName}`,
            shopId: sid,
            status: 'completed',
            createdAt: new Date(),
            transactionId: `del_mtr_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
            cashierName
         });

         const userDoc = await userDataService.getUserData(user.uid);
         const currentCash = Number(userDoc?.cashBalance || 0);
         const newCash = currentCash - receipt.retailPrice;
         await userDataService.updateUserData(user.uid, { cashBalance: newCash });
      }

      toast({ title: 'تم الحذف', description: 'تم حذف الإيصال واسترجاع الأرصدة.' });

    } catch (error) {
      console.error("Error deleting transfer receipt:", error);
      setTransferReceipts(oldReceipts);
      toast({ title: 'خطأ', description: 'فشل حذف الإيصال.', variant: 'destructive' });
    }
  };

  const handleDeleteMachineReceipt = (id: string) => {
    setReceipts(prev => prev.filter(r => r.id !== id));
    toast({ title: 'تم حذف الإيصال', description: 'تم إزالة إيصال الماكينة المحدد.' });
  };

  // زر تصفير الرصيد الافتتاحي لتغييره
  const handleResetOpening = () => {
    setSavedOpeningBase(null);
    setSavedOpeningAir(null);
    setSavedDrawerCash(null);
    setOpeningSnapshot(null);
  
    setOpeningBase('');
    setOpeningAir('');
    setDrawerCash('');
  
    try {
      const keyOpening = selectedMachineId ? `${STORAGE_KEY_OPENING}_${selectedMachineId}` : STORAGE_KEY_OPENING;
      const keySnap = selectedMachineId ? `${STORAGE_KEY_OPENING_SNAPSHOT}_${selectedMachineId}` : STORAGE_KEY_OPENING_SNAPSHOT;
      localStorage.removeItem(keyOpening);
      localStorage.removeItem(keySnap);
    } catch {}
  
    toast({ title: 'تم التصفير', description: 'يمكنك الآن إدخال رصيد افتتاحي جديد.' });
  };
  const handleAddOpening = async () => {
    const base = parseFloat(openingBase || '0');
    const air = parseFloat(openingAir || '0');
    const dc = parseFloat(drawerCash || '0');

    if (isNaN(base) || isNaN(air) || isNaN(dc)) {
      toast({ title: 'قيمة غير صالحة', description: 'يرجى إدخال أرقام صحيحة للرصيد الافتتاحي وفلوس الدرج.', variant: 'destructive' });
      return;
    }
    if (base < 0 || air < 0 || dc < 0) {
      toast({ title: 'قيمة سالبة', description: 'لا يمكن إدخال قيم سالبة.', variant: 'destructive' });
      return;
    }

    setSavedOpeningBase(base);
    setSavedOpeningAir(air);
    setSavedDrawerCash(dc);
    try {
      const keyOpening = selectedMachineId ? `${STORAGE_KEY_OPENING}_${selectedMachineId}` : STORAGE_KEY_OPENING;
      const keySnap = selectedMachineId ? `${STORAGE_KEY_OPENING_SNAPSHOT}_${selectedMachineId}` : STORAGE_KEY_OPENING_SNAPSHOT;
      localStorage.setItem(keyOpening, JSON.stringify({ openingBase: base, openingAir: air, drawerCash: dc }));
      localStorage.setItem(keySnap, JSON.stringify({ openingBase: base, openingAir: air, drawerCash: dc }));
      setOpeningSnapshot({ openingBase: base, openingAir: air, drawerCash: dc });
      // حفظ في Firestore تحت الماكينة المحددة
      const sid0 = shopId || getFallbackShopId();
      if (sid0 && selectedMachineId) {
        const ref = doc(db, 'shops', sid0, 'machines', selectedMachineId);
        await setDoc(ref, {
          name: machines.find(m => m.id === selectedMachineId)?.name || 'ماكينة',
          openingBase: base,
          openingAir: air,
          drawerCash: dc,
          cashierName,
          shopName: shopName || null,
          updatedAt: serverTimestamp(),
        }, { merge: true });
      }
    } catch {}

    toast({ title: 'تم تسجيل البيانات', description: `الافتتاحي الأساسي: ${formatNumber(base)}، الهواء: ${formatNumber(air)}، فلوس الدرج: ${formatNumber(dc)}` });
  };

  const handleStartDelivery = () => {
    if (savedOpeningBase == null || savedOpeningAir == null) {
      toast({ title: 'سجل الرصيد الافتتاحي أولاً', description: 'يرجى الضغط على زر "إضافة" بعد إدخال الافتتاحي.' });
      return;
    }
    setActiveSection('delivery');
  };

  const handleDoDelivery = () => {
    const dBase = parseFloat(deliveryBase || '0');
    const dAir = parseFloat(deliveryAir || '0');

    if (isNaN(dBase) || isNaN(dAir)) {
      toast({ title: 'قيمة غير صالحة', description: 'يرجى إدخال أرقام صحيحة لرصيد التسليم.', variant: 'destructive' });
      return;
    }
    if (dBase < 0 || dAir < 0) {
      toast({ title: 'قيمة سالبة', description: 'لا يمكن إدخال قيم سالبة.', variant: 'destructive' });
      return;
    }

    const openingBaseForReceipt = openingSnapshot?.openingBase ?? (savedOpeningBase || 0);
    const openingAirForReceipt = openingSnapshot?.openingAir ?? (savedOpeningAir || 0);

    const openingSum = (openingBaseForReceipt) + (openingAirForReceipt);
    const deliverySum = dBase + dAir;
    const result = deliverySum - openingSum;

    setNetResult(result);

    const receipt: MachineReceipt = {
      id: `${Date.now()}`,
      openingBase: openingBaseForReceipt,
      openingAir: openingAirForReceipt,
      deliveryBase: dBase,
      deliveryAir: dAir,
      netResult: result,
      createdAt: new Date().toISOString(),
      cashierName,
      requiredDrawerNoProfit: result < 0 ? Math.round(-result * 100) / 100 : 0,
    };
    latestDeliveryId.current = receipt.id;

    // حفظ التسليم في Firestore تحت الماكينة المحددة
    void (async () => {
      try {
        const sid1 = shopId || getFallbackShopId();
        if (sid1 && selectedMachineId) {
          await addDoc(collection(db, 'shops', sid1, 'machines', selectedMachineId, 'deliveries'), {
            cashierName: cashierName || 'Unknown',
            userId: user?.uid, // إضافة معرف المستخدم للصلاحيات
            openingBase: openingBaseForReceipt,
            openingAir: openingAirForReceipt,
            deliveryBase: dBase,
            deliveryAir: dAir,
            netResult: result,
            createdAt: serverTimestamp(),
          });
          await setDoc(doc(db, 'shops', sid1, 'machines', selectedMachineId), {
            lastDeliveryAt: serverTimestamp(),
            updatedAt: serverTimestamp(),
          }, { merge: true });

          // تسجيل تسليم الوردية في المعاملات الأخيرة
          if (user?.uid) {
            const selectedMachineName = machines.find(m => m.id === selectedMachineId)?.name || 'ماكينة';
            await userDataService.saveUserTransaction(user.uid, {
              type: 'machine_delivery',
              amount: result,
              senderName: selectedMachineName,
              title: 'تسليم وردية مكنه',
              description: `تسليم وردية مكنه: ${selectedMachineName}`,
              shopId: sid1,
              status: 'completed',
              createdAt: new Date(),
              transactionId: `mdel_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
              reference: `MDL-${Date.now()}`,
              cashierName: cashierName || 'Unknown',
              details: {
                openingBase: openingBaseForReceipt,
                openingAir: openingAirForReceipt,
                deliveryBase: dBase,
                deliveryAir: dAir
              }
            });
          }

          // تحديث الحالة المحلية فقط بعد نجاح الحفظ في السيرفر
          setReceipts((prev) => [receipt, ...prev]);
          try {
            const sid = shopId || getFallbackShopId();
            const key = sid && selectedMachineId ? `machineDaily_deliveries_${sid}_${selectedMachineId}` : `machineDaily_deliveries_${selectedMachineId}`;
            const current = localStorage.getItem(key);
            const arr = current ? JSON.parse(current) : [];
            const next = [receipt, ...(Array.isArray(arr) ? arr : [])];
            localStorage.setItem(key, JSON.stringify(next));
          } catch {}

          toast({ title: 'تم إنشاء إيصال تسليم', description: `الناتج النهائي: ${formatNumber(result)}` });
        }
      } catch (err) {
        console.error('Persist delivery error:', err);
        toast({ title: 'خطأ', description: 'حدث خطأ أثناء حفظ التسليم. تأكد من الصلاحيات.', variant: 'destructive' });
      }
    })();

    // إزالة التحديث المحلي المتفائل (Optimistic Update) من هنا لأنه يسبب "ظهور ثم اختفاء" إذا فشل السيرفر
    // سيتم التحديث داخل الـ try block أعلاه
    /*
    setReceipts((prev) => [receipt, ...prev]);
    try {
      const sid = shopId || getFallbackShopId();
      const key = sid && selectedMachineId ? `machineDaily_deliveries_${sid}_${selectedMachineId}` : `machineDaily_deliveries_${selectedMachineId}`;
      const current = localStorage.getItem(key);
      const arr = current ? JSON.parse(current) : [];
      const next = [receipt, ...(Array.isArray(arr) ? arr : [])];
      localStorage.setItem(key, JSON.stringify(next));
    } catch {}
    
    toast({ title: 'تم إنشاء إيصال تسليم', description: `الناتج النهائي: ${formatNumber(result)}` });
    */
  };

  const handleTransferConfirm = () => {
    const w = parseFloat(wholesalePrice || '0');
    const r = parseFloat(retailPrice || '0');
    if (!Number.isFinite(w) || !Number.isFinite(r)) {
      toast({ title: 'قيمة غير صالحة', description: 'يرجى إدخال أرقام صحيحة لسعر الجملة وسعر التجزئة.', variant: 'destructive' });
      return;
    }
    if (w < 0 || r < 0) {
      toast({ title: 'قيمة سالبة', description: 'لا يمكن إدخال قيم سالبة.', variant: 'destructive' });
      return;
    }

    if (savedOpeningBase == null || savedOpeningAir == null) {
      toast({ title: 'سجل الرصيد الافتتاحي أولاً', description: 'يرجى إدخال الرصيد الأساسي ورصيد الهواء ثم الضغط "إضافة".' });
      return;
    }

    const profit = Math.round((r - w) * 100) / 100;
    setPendingTransfer({ wholesalePrice: w, retailPrice: r, profit });
    setDeductDialogOpen(true);
  };

  const finalizeTransfer = (from: 'base' | 'air') => {
    if (isProcessingRef.current) return;
    if (!pendingTransfer) return;

    const transfer = pendingTransfer;
    const nowTs = Date.now();
    const tempId = `temp_mtr_${nowTs}`;
    const referenceId = `MTR-${nowTs}`;

    const receipt: TransferReceipt = {
      id: `transfer_${Date.now()}`,
      wholesalePrice: transfer.wholesalePrice,
      retailPrice: transfer.retailPrice,
      profit: transfer.profit,
      createdAt: new Date().toISOString(),
      cashierName,
      deductFrom: from,
    };
    latestTransferId.current = receipt.id;

    const stockDeductAmount = transfer.wholesalePrice;
    const prevBase = savedOpeningBase ?? 0;
    const prevAir = savedOpeningAir ?? 0;
    const prevDrawer = savedDrawerCash ?? 0;

    const availableStock = from === 'base' ? prevBase : prevAir;
    if (stockDeductAmount > availableStock) {
      toast({ title: 'رصيد غير كافٍ', description: 'لا يوجد رصيد كافٍ للخصم بسعر الجملة.', variant: 'destructive' });
      return;
    }

    const newBase = from === 'base' ? Math.round((prevBase - stockDeductAmount) * 100) / 100 : prevBase;
    const newAir = from === 'air' ? Math.round((prevAir - stockDeductAmount) * 100) / 100 : prevAir;
    const totalDrawerAdded = transfer.retailPrice;
    const newDrawer = Math.round((prevDrawer + totalDrawerAdded) * 100) / 100;

    isProcessingRef.current = true;
    setIsProcessing(true);

    setSavedOpeningBase(newBase);
    setSavedOpeningAir(newAir);
    setSavedDrawerCash(newDrawer);

    try {
      const keyOpening = selectedMachineId ? `${STORAGE_KEY_OPENING}_${selectedMachineId}` : STORAGE_KEY_OPENING;
      const keySnap = selectedMachineId ? `${STORAGE_KEY_OPENING_SNAPSHOT}_${selectedMachineId}` : STORAGE_KEY_OPENING_SNAPSHOT;
      localStorage.setItem(keyOpening, JSON.stringify({ openingBase: newBase, openingAir: newAir, drawerCash: newDrawer }));
      localStorage.setItem(keySnap, JSON.stringify({ openingBase: newBase, openingAir: newAir, drawerCash: newDrawer }));
    } catch {}

    setTransferReceipts(prev => [receipt, ...prev]);
    try {
      const sidKey = shopId || getFallbackShopId();
      const key = sidKey && selectedMachineId ? `machineDaily_transfers_${sidKey}_${selectedMachineId}` : 'machineDaily_transfers';
      const current = localStorage.getItem(key);
      const arr = current ? JSON.parse(current) : [];
      const next = [receipt, ...(Array.isArray(arr) ? arr : [])];
      localStorage.setItem(key, JSON.stringify(next));
    } catch {}

    setTransferDialogOpen(false);
    setWholesalePrice('');
    setRetailPrice('');
    setPendingTransfer(null);
    setDeductDialogOpen(false);

    toast({
      title: 'تم تسجيل تحويل',
      description: `الربح الفوري: ${formatNumber(receipt.profit)} | خصم المخزون: -${formatNumber(stockDeductAmount)} (${from === 'base' ? 'الأساسي' : 'الهواء'}) | درج (المباع فقط): +${formatNumber(totalDrawerAdded)}`,
    });

    const sid2 = shopId || getFallbackShopId();

    (async () => {
      try {
        if (sid2 && selectedMachineId) {
          const machineRef = doc(db, 'shops', sid2, 'machines', selectedMachineId);
          await setDoc(
            machineRef,
            {
              openingBase: newBase,
              openingAir: newAir,
              drawerCash: newDrawer,
              cashierName,
              updatedAt: serverTimestamp(),
            },
            { merge: true }
          );

          await addDoc(collection(db, 'shops', sid2, 'machines', selectedMachineId, 'transfers'), {
            wholesalePrice: transfer.wholesalePrice,
            retailPrice: transfer.retailPrice,
            profit: transfer.profit,
            deductFrom: from,
            cashierName,
            userId: user?.uid,
            createdAt: serverTimestamp(),
          });

          if (user?.uid) {
            const selectedMachineName = machines.find(m => m.id === selectedMachineId)?.name || 'ماكينة';
            const transferAmount = transfer.retailPrice;

            await userDataService.saveUserTransaction(user.uid, {
              type: 'transfer',
              amount: transferAmount,
              senderName: selectedMachineName,
              title: 'إيصال تحويل مكينه',
              description: `تحويل مكنه من ${selectedMachineName}`,
              shopId: sid2,
              status: 'completed',
              createdAt: new Date(),
              transactionId: `mtr_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
              reference: referenceId,
              cashierName,
            });

            try {
              const userDoc = await userDataService.getUserData(user.uid);
              const currentCash = Number(userDoc?.cashBalance || 0);
              const newCash = currentCash + transferAmount;

              await userDataService.updateUserData(user.uid, { cashBalance: newCash });

              try {
                if (sid2) {
                  const { getDoc, updateDoc } = await import('firebase/firestore');
                  const dashRef = doc(db, 'dashboardData', sid2);
                  const dashSnap = await getDoc(dashRef);
                  if (dashSnap.exists()) {
                    const dashCash = Number(dashSnap.data().cashBalance || 0);
                    await updateDoc(dashRef, {
                      cashBalance: dashCash + transferAmount,
                      lastUpdated: new Date().toISOString(),
                    } as any);
                  }
                }
              } catch {}

              try {
                localStorage.setItem(`cashBalance_${user.uid}`, String(newCash));
                window.dispatchEvent(new CustomEvent('cashBalanceChanged', { detail: { value: newCash } }));
              } catch {}

              try {
                const optimisticTx: any = {
                  id: tempId,
                  type: 'transfer',
                  amount: transferAmount,
                  status: 'completed',
                  createdAt: new Date(),
                  title: 'إيصال تحويل مكينه',
                  description: `تحويل مكنه من ${selectedMachineName}`,
                  reference: referenceId,
                  senderName: selectedMachineName,
                  shopId: sid2,
                  cashierName,
                };
                window.dispatchEvent(new CustomEvent('dashboard:external-update', { detail: { newTransaction: optimisticTx } }));
                window.dispatchEvent(new CustomEvent('transaction-created'));
              } catch {}
            } catch (err) {
              console.error('Error updating cash balance for machine transfer:', err);
            }
          }
        } else {
          console.error('Missing shopId or selectedMachineId', { sid2, selectedMachineId });
          toast({ title: 'تنبيه', description: 'قد لا يتم حفظ البيانات في السيرفر لعدم توفر معرف المحل أو الماكينة', variant: 'destructive' });
        }
      } catch (error) {
        console.error('Error finalizing transfer:', error);
        toast({ title: 'خطأ', description: 'حدث خطأ أثناء حفظ التحويل في قاعدة البيانات', variant: 'destructive' });
      } finally {
        isProcessingRef.current = false;
        setIsProcessing(false);
      }
    })();
  };

  const resetDialogState = () => {
    setOpeningBase('');
    setOpeningAir('');
    setDeliveryBase('');
    setDeliveryAir('');
    setNetResult(null);
  };


  const refreshMachineData = async () => {
    if (!open || !selectedMachineId) return;
    const sid = shopId || getFallbackShopId();
    if (!sid) return;

    // Fetch Deliveries
    try {
      const col = collection(db, 'shops', sid, 'machines', selectedMachineId, 'deliveries');
      console.log(`Fetching deliveries from: shops/${sid}/machines/${selectedMachineId}/deliveries`);
      const snap = await getDocs(query(col));
      console.log(`Found ${snap.docs.length} delivery documents`);
      const list: MachineReceipt[] = snap.docs.map((d) => {
        const x: any = d.data();
        const created = x?.createdAt?.toDate?.() ? x.createdAt.toDate() : new Date();
        return {
          id: d.id,
          openingBase: Number(x?.openingBase || 0),
          openingAir: Number(x?.openingAir || 0),
          deliveryBase: Number(x?.deliveryBase || 0),
          deliveryAir: Number(x?.deliveryAir || 0),
          netResult: typeof x?.netResult === 'number'
            ? Number(x.netResult)
            : Math.round(((Number(x?.deliveryBase || 0) + Number(x?.deliveryAir || 0)) - (Number(x?.openingBase || 0) + Number(x?.openingAir || 0))) * 100) / 100,
          createdAt: created.toISOString(),
          cashierName: x?.cashierName || undefined,
          requiredDrawerNoProfit: typeof x?.requiredDrawerNoProfit === 'number' ? x.requiredDrawerNoProfit : undefined,
        } as MachineReceipt;
      });
      const sorted = [...list].sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());

      // دمج الإيصال الأخير إذا لم يكن موجوداً في القائمة القادمة من السيرفر (بسبب تأخر الفهرسة)
      setReceipts(prev => {
        // إذا كان لدينا إيصال تم إنشاؤه محلياً مؤخراً
        if (latestDeliveryId.current) {
          const inServer = sorted.find(r => r.id === latestDeliveryId.current);
          if (!inServer) {
            // نبحث عنه في الحالة الحالية
            const local = prev.find(r => r.id === latestDeliveryId.current);
            if (local) {
              // نضيفه للقائمة الجديدة
              const merged = [local, ...sorted].sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());
              // إزالة التكرار إن وجد
              const unique = merged.filter((item, index, self) => index === self.findIndex((t) => (t.id === item.id)));
              return unique;
            }
          }
        }
        return sorted;
      });

      try { localStorage.setItem(`machineDaily_deliveries_${sid}_${selectedMachineId}`, JSON.stringify(sorted)); } catch {}
    } catch (e) {
      console.error('Error fetching deliveries:', e);
    }

    // Fetch Transfers
    try {
      const col = collection(db, 'shops', sid, 'machines', selectedMachineId, 'transfers');
      console.log(`Fetching transfers from: shops/${sid}/machines/${selectedMachineId}/transfers`);
      const snap = await getDocs(query(col));
      console.log(`Found ${snap.docs.length} transfer documents`);
      const list: TransferReceipt[] = snap.docs.map((d) => {
        const x: any = d.data();
        const created = x?.createdAt?.toDate?.() ? x.createdAt.toDate() : new Date();
        const ded: any = x?.deductFrom;
        return {
          id: d.id,
          wholesalePrice: Number(x?.wholesalePrice || 0),
          retailPrice: Number(x?.retailPrice || 0),
          profit: Number(x?.profit ?? ((Number(x?.retailPrice || 0) - Number(x?.wholesalePrice || 0)) || 0)),
          createdAt: created.toISOString(),
          cashierName: x?.cashierName || undefined,
          deductFrom: ded === 'base' || ded === 'air' ? ded : undefined,
        } as TransferReceipt;
      });
      const sorted = [...list].sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());
      setTransferReceipts(sorted);

      try { localStorage.setItem(`machineDaily_transfers_${sid}_${selectedMachineId}`, JSON.stringify(sorted)); } catch {}
    } catch (e) {
      console.error('Error fetching transfers:', e);
      toast({ title: 'خطأ', description: 'فشل في جلب التحويلات من السيرفر', variant: 'destructive' });
    }
    
    toast({ title: 'تم التحديث', description: 'تم جلب أحدث البيانات.' });
  };

  useEffect(() => {
    if (!open || !selectedMachineId) return;
    const sid = shopId || getFallbackShopId();
    // Load from cache first
    try {
      const keyDel = sid ? `machineDaily_deliveries_${sid}_${selectedMachineId}` : `machineDaily_deliveries_${selectedMachineId}`;
      const cachedDel = localStorage.getItem(keyDel);
      if (cachedDel) {
        const parsed = JSON.parse(cachedDel);
        if (Array.isArray(parsed)) setReceipts(parsed);
      }
      
      const keyTrans = sid ? `machineDaily_transfers_${sid}_${selectedMachineId}` : `machineDaily_transfers_${selectedMachineId}`;
      const cachedTrans = localStorage.getItem(keyTrans);
      if (cachedTrans) {
        const parsed = JSON.parse(cachedTrans);
        if (Array.isArray(parsed)) setTransferReceipts(parsed);
      }
    } catch {}

    // Initial Fetch
    refreshMachineData();
  }, [open, shopId, selectedMachineId]);


  // جلب مسحوبات الشهر الحالي واليومي
  const fetchMonthlyWithdrawals = async () => {
    if (!selectedMachineId || !shopId) return;
    try {
      const now = new Date();
      const startOfMonth = new Date(now.getFullYear(), now.getMonth(), 1);
      const startOfDay = new Date(now.getFullYear(), now.getMonth(), now.getDate()); // 12:00 AM today
      
      const col = collection(db, 'shops', shopId, 'machines', selectedMachineId, 'withdrawals');
      const q = query(col, orderBy('createdAt', 'desc'));
      const snap = await getDocs(q);
      
      const list = snap.docs.map(doc => {
        const d = doc.data();
        return { 
            id: doc.id, 
            ...d,
            createdAt: d.createdAt?.toDate ? d.createdAt.toDate() : new Date(d.createdAt)
        };
      });
      setWithdrawalReceipts(list);

      let mSum = 0;
      let dSum = 0;
      let mProfit = 0;
      let dProfit = 0;

      list.forEach((item) => {
        const created = item.createdAt;
        const amount = Number(item.amount) || 0;
        const profit = Number(item.commission) || 0;
        
        if (created >= startOfMonth) {
          mSum += amount;
          mProfit += profit;
        }
        if (created >= startOfDay) {
          dSum += amount;
          dProfit += profit;
        }
      });
      
      setMonthlyWithdrawalsUsed(mSum);
      setDailyWithdrawalsUsed(dSum);
      setMonthlyWithdrawalsProfit(mProfit);
      setDailyWithdrawalsProfit(dProfit);
    } catch (err) {
      console.error('Error fetching monthly withdrawals:', err);
    }
  };

  useEffect(() => {
    if ((activeSection === 'cashWithdrawal' || activeSection === 'withdrawalReceipts') && selectedMachineId) {
      fetchMonthlyWithdrawals();
    }
  }, [activeSection, selectedMachineId, shopId]);

  const handleDeleteWithdrawalReceipt = async (id: string) => {
    if (!selectedMachineId || !shopId) return;
    try {
        const docRef = doc(db, 'shops', shopId, 'machines', selectedMachineId, 'withdrawals', id);
        // التحقق مما إذا كان قد تم الخصم من الدرج لاسترجاع المبلغ
        const { getDoc } = await import('firebase/firestore');
        const snap = await getDoc(docRef);
        
        if (snap.exists()) {
           const data = snap.data();
           if (data.deductedFromDrawer && data.userId && data.amount) {
              // استرجاع المبلغ للدرج
              const userDoc = await userDataService.getUserData(data.userId);
              const currentCash = Number(userDoc?.cashBalance || 0);
              const newCash = currentCash + Number(data.amount);
              
              await userDataService.updateUserData(data.userId, { cashBalance: newCash });
              try {
                  const { updateDoc } = await import('firebase/firestore');
                  const shopDocRef = doc(db, 'dashboardData', shopId);
                  await updateDoc(shopDocRef, { cashBalance: newCash } as any);
              } catch (e) { console.error('Error updating shop cash balance:', e); }
           }
        }

        await deleteDoc(docRef);
        toast({ title: 'تم الحذف', description: 'تم حذف إيصال السحب' });
        fetchMonthlyWithdrawals();
    } catch (err) {
        console.error('Delete withdrawal error:', err);
        toast({ title: 'خطأ', description: 'فشل الحذف', variant: 'destructive' });
    }
  };

  const handleCashWithdrawal = async () => {
    if (isProcessingRef.current) return;
    isProcessingRef.current = true;
    setIsProcessing(true);

    try {
      const amount = parseFloat(withdrawalAmount);
      
      if (isNaN(amount) || amount <= 0) {
        toast({ title: 'خطأ', description: 'يرجى إدخال مبلغ صحيح', variant: 'destructive' });
        return;
      }
      
      let commission = 0;
      let rateUsed = 0;
  
      if (commissionMode === 'rate') {
          const rate = parseFloat(commissionRate);
          if (isNaN(rate) || rate < 0) {
            toast({ title: 'خطأ', description: 'يرجى إدخال عمولة صحيحة', variant: 'destructive' });
            return;
          }
          commission = (amount / 1000) * rate;
          rateUsed = rate;
      } else {
          const fixed = parseFloat(fixedCommissionAmount);
          if (isNaN(fixed) || fixed < 0) {
              toast({ title: 'خطأ', description: 'يرجى إدخال مبلغ عمولة صحيح', variant: 'destructive' });
              return;
          }
          commission = fixed;
      }
  
      if (!selectedMachineId || !shopId) {
        toast({ title: 'خطأ', description: 'بيانات المحل أو الماكينة غير متوفرة', variant: 'destructive' });
        return;
      }
  
      let updatedCashBalance: number | null = null;
  
      try {
        // خصم من درج النقدية إذا تم اختياره
        if (deductFromDrawer && user?.uid) {
           const userDoc = await userDataService.getUserData(user.uid);
           const currentCash = Number(userDoc?.cashBalance || 0);
           const newCash = currentCash - amount;
           updatedCashBalance = newCash;
           
           await userDataService.updateUserData(user.uid, { cashBalance: newCash });
           try {
               const { updateDoc } = await import('firebase/firestore');
               const shopDocRef = doc(db, 'dashboardData', shopId);
               await updateDoc(shopDocRef, { cashBalance: newCash } as any);
           } catch (e) { console.error('Error updating shop cash balance:', e); }
        }
  
        // حفظ المعاملة
        await addDoc(collection(db, 'shops', shopId, 'machines', selectedMachineId, 'withdrawals'), {
          amount,
          commissionMode, 
          commissionRate: commissionMode === 'rate' ? rateUsed : 0,
          fixedCommission: commissionMode === 'fixed' ? commission : 0,
          commission,
          createdAt: serverTimestamp(),
          userId: user?.uid,
          cashierName,
          deductedFromDrawer: deductFromDrawer
        });
  
        // تسجيل المعاملة في لوحة المستخدم
        if (user?.uid) {
          try {
              const selectedMachineName = machines.find(m => m.id === selectedMachineId)?.name || 'ماكينة';
              const txData = {
                  type: 'machine_withdrawal',
                  amount: amount,
                  commission: commission,
                  senderName: selectedMachineName,
                  title: 'إيصال سحب فوري',
                  description: `سحب كاش بقيمة ${formatNumber(amount)} وعمولة ${formatNumber(commission)}${deductFromDrawer ? ' (تم الخصم من الدرج)' : ''}`,
                  shopId: shopId,
                  status: 'completed',
                  createdAt: new Date(),
                  transactionId: `mwd_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
                  reference: `MWD-${Date.now()}-${Math.random().toString(36).substr(2, 5)}`,
                  cashierName
              };

              const docId = await userDataService.saveUserTransaction(user.uid, txData);

              // تحديث كائن المعاملة بالمعرف الحقيقي من Firestore
              const finalTxData = { ...txData, id: docId };
  
              // إضافة الربح للتقارير
              // if (commission > 0) {
              //    ProfitService.addProfit(commission, user.uid);
              // }
  
              // Dispatch events for real-time updates
              try {
                window.dispatchEvent(new CustomEvent('dashboard:external-update', { detail: { newTransaction: finalTxData } }));
                window.dispatchEvent(new CustomEvent('transaction-created'));
                // window.dispatchEvent(new CustomEvent('newTransactionAdded', { detail: { transaction: finalTxData } }));
  
                if (updatedCashBalance !== null) {
                   window.dispatchEvent(new CustomEvent('cashBalanceChanged', { detail: { value: updatedCashBalance } }));
                }
              } catch (e) { console.error('Error dispatching events:', e); }
  
          } catch (txError) {
              console.error('Failed to save user transaction log:', txError);
          }
        }
  
        toast({ title: 'تم السحب', description: `تم سحب ${formatNumber(amount)} وعمولة ${formatNumber(commission)}` });
        setWithdrawalAmount('');
        fetchMonthlyWithdrawals(); 
        
      } catch (err) {
        console.error('Withdrawal error:', err);
        toast({ title: 'خطأ', description: 'فشل في تنفيذ السحب', variant: 'destructive' });
      }
    } finally {
        isProcessingRef.current = false;
        setIsProcessing(false);
    }
  };

  const handleClose = (nextOpen: boolean) => {
    if (!nextOpen) {
      // عند الغلق، لا نمسح القيم المسجلة حتى لا يفقد المستخدم الافتتاحي
      resetDialogState();
    }
    onOpenChange(nextOpen);
  };

  return (
    <Dialog open={open} onOpenChange={handleClose}>
      <DialogContent className="w-screen h-[100vh] overflow-hidden rounded-none p-0 flex flex-col dark:bg-[#121212]">
        <DialogHeader className="sticky top-0 bg-white dark:bg-[#0F0F0F] z-20 border-b border-gray-200 dark:border-[#333] px-4 py-3">
          <DialogTitle className="flex items-center justify-between text-right">
            <span className="flex items-center gap-2">
              <FawryMachineIcon className="h-5 w-5 text-yellow-500" />
              يومية المكنة {selectedMachineId && machines.find(m => m.id === selectedMachineId)?.name ? `- ${machines.find(m => m.id === selectedMachineId)?.name}` : ''}
            </span>
            <div className="flex items-center gap-1">
              <Button variant="ghost" onClick={() => setIsVideoModalOpen(true)} title="شرح فيديو" className="gap-1 px-2 text-rose-600 hover:text-rose-700 hover:bg-rose-50">
                <PlayCircle className="h-5 w-5" />
                <span className="font-medium">شرح قسم المكن فديو</span>
              </Button>
              <Button variant="ghost" size="icon" onClick={refreshMachineData} title="تحديث البيانات">
                <RefreshCcw className="h-4 w-4" />
              </Button>
            </div>
          </DialogTitle>
          <DialogDescription className="text-right">
            أدخل الرصيد الافتتاحي، ثم في نهاية اليوم قم بإدخال رصيد التسليم لإنشاء إيصال منظم باسم "إيصالات ماكينة".
          </DialogDescription>
          <div className="mt-2 grid grid-cols-2 sm:flex sm:flex-wrap sm:justify-end gap-2">
            <Button
              size="sm"
              variant="outline"
              className={`w-full sm:w-auto ${activeSection === 'machines' ? 'bg-violet-600 text-white hover:bg-violet-700' : 'border-violet-300 text-violet-700'}`}
              onClick={() => setActiveSection('machines')}
            >
              الماكينات
            </Button>
            <Button
              size="sm"
              variant="outline"
              className={`w-full sm:w-auto ${activeSection === 'cashWithdrawal' ? 'bg-violet-600 text-white hover:bg-violet-700' : 'border-violet-300 text-violet-700'}`}
              onClick={() => setActiveSection('cashWithdrawal')}
            >
              سحب كاش
            </Button>
            <Button
              size="sm"
              variant="outline"
              className={`w-full sm:w-auto ${activeSection === 'withdrawalReceipts' ? 'bg-violet-600 text-white hover:bg-violet-700' : 'border-violet-300 text-violet-700'}`}
              onClick={() => setActiveSection('withdrawalReceipts')}
            >
              إيصالات سحب مكنة
            </Button>
            <Button
              size="sm"
              variant="outline"
              className={`w-full sm:w-auto ${activeSection === 'opening' ? 'bg-violet-600 text-white hover:bg-violet-700' : 'border-violet-300 text-violet-700'}`}
              onClick={() => setActiveSection('opening')}
            >
              الرصيد الافتتاحي
            </Button>
            <Button
              size="sm"
              variant="outline"
              className={`w-full sm:w-auto ${activeSection === 'transfer' ? 'bg-violet-600 text-white hover:bg-violet-700' : 'border-violet-300 text-violet-700'}`}
              onClick={() => setActiveSection('transfer')}
            >
              التحويل
            </Button>
            <Button
              size="sm"
              variant="outline"
              className={`w-full sm:w-auto ${activeSection === 'transferReceipts' ? 'bg-violet-600 text-white hover:bg-violet-700' : 'border-violet-300 text-violet-700'}`}
              onClick={() => setActiveSection('transferReceipts')}
            >
              إيصالات التحويل
            </Button>
            <Button
              size="sm"
              variant="outline"
              className={`w-full sm:w-auto ${activeSection === 'delivery' ? 'bg-violet-600 text-white hover:bg-violet-700' : 'border-violet-300 text-violet-700'}`}
              onClick={() => setActiveSection('delivery')}
            >
              تسليم الوردية
            </Button>
            <Button
              size="sm"
              variant="outline"
              className={`w-full sm:w-auto col-span-2 sm:col-auto ${activeSection === 'deliveryReceipts' ? 'bg-violet-600 text-white hover:bg-violet-700' : 'border-violet-300 text-violet-700'}`}
              onClick={() => setActiveSection('deliveryReceipts')}
            >
              إيصالات التسليم
            </Button>
          </div>
        </DialogHeader>

        <div className="px-4 py-3 overflow-y-auto flex-1 grid grid-cols-1 gap-6">
          {/* إدارة الماكينات */}
          {activeSection === 'machines' && (
          <Card>
            <CardHeader className="pb-2">
          <CardTitle className="flex items-center justify-between">
            <span className="text-right">إدارة الماكينات</span>
            <div className="flex items-center gap-2">
              <Button
                variant="destructive"
                onClick={async () => {
                  try {
                    if (!shopId || !selectedMachineId) return;
                    const baseRef = doc(db, 'shops', shopId, 'machines', selectedMachineId);
                    try {
                      const transSnap = await getDocs(collection(db, 'shops', shopId, 'machines', selectedMachineId, 'transfers'));
                      await Promise.all(transSnap.docs.map(d => deleteDoc(d.ref)));
                    } catch {}
                    try {
                      const delSnap = await getDocs(collection(db, 'shops', shopId, 'machines', selectedMachineId, 'deliveries'));
                      await Promise.all(delSnap.docs.map(d => deleteDoc(d.ref)));
                    } catch {}
                    await deleteDoc(baseRef);
                    setMachines(prev => prev.filter(m => m.id !== selectedMachineId));
                    const next = machines.find(m => m.id !== selectedMachineId);
                    setSelectedMachineId(next ? next.id : null);
                    try {
                      const keyOpening = `${STORAGE_KEY_OPENING}_${selectedMachineId}`;
                      const keySnap = `${STORAGE_KEY_OPENING_SNAPSHOT}_${selectedMachineId}`;
                      localStorage.removeItem(keyOpening);
                      localStorage.removeItem(keySnap);
                    } catch {}
                    toast({ title: 'تم حذف الماكينة', description: 'تمت الإزالة نهائيًا.' });
                  } catch (err) {
                    toast({ title: 'فشل الحذف', description: 'تعذر حذف الماكينة.' , variant: 'destructive' });
                  }
                }}
                disabled={!selectedMachineId}
                title="حذف الماكينة"
              >
                <Trash2 className="h-4 w-4" />
              </Button>
            </div>
          </CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 items-end">
                <div>
                  <label className="text-sm font-medium block text-right">الماكينة الحالية</label>
                  <Select value={selectedMachineId ?? ''} onValueChange={(v) => { setSelectedMachineId(v); try { const sidSel = shopId || getFallbackShopId(); if (sidSel) localStorage.setItem(`machineDaily_selected_${sidSel}`, v); } catch {} }}>
                    <SelectTrigger className="text-right">
                      <SelectValue placeholder="اختر ماكينة" />
                    </SelectTrigger>
                    <SelectContent>
                      {machines.map(m => (
                        <SelectItem key={m.id} value={m.id}>{m.name}</SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-2">
                  <div className="md:col-span-2">
                    <label className="text-sm font-medium block text-right">إنشاء ماكينة جديدة</label>
                    <Input value={newMachineName} onChange={(e) => setNewMachineName(e.target.value)} placeholder="اسم الماكينة" className="text-right" />
                  </div>
                  <div className="flex justify-end md:justify-start">
                    <Button
                      onClick={async () => {
                        try {
                          if (!shopId) {
                            toast({ title: 'لا يوجد محل', description: 'يرجى التأكد من تحميل بيانات المحل.', variant: 'destructive' });
                            return;
                          }
                          const name = (newMachineName || '').trim();
                          if (!name) {
                            toast({ title: 'اسم الماكينة مطلوب', description: 'أدخل اسم للماكينة الجديدة.', variant: 'destructive' });
                            return;
                          }
                          const ref = await addDoc(collection(db, 'shops', shopId, 'machines'), {
                            name,
                            shopId,
                            createdAt: serverTimestamp(),
                            updatedAt: serverTimestamp(),
                            isActive: true,
                          });
                          const created = { id: ref.id, name };
                          setMachines(prev => [created, ...prev]);
                          setSelectedMachineId(ref.id);
                          setNewMachineName('');
                          toast({ title: 'تم إنشاء ماكينة', description: `تمت إضافة ${name}` });
                        } catch (err) {
                          console.error('Create machine error:', err);
                          toast({ title: 'خطأ في الإنشاء', description: 'تعذر إنشاء الماكينة.', variant: 'destructive' });
                        }
                      }}
                      className="bg-violet-600 hover:bg-violet-700"
                    >
                      إضافة
                    </Button>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
          )}
          {activeSection === 'opening' && (
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="flex items-center justify-between">
                <span className="text-right">الرصيد الافتتاحي</span>
              </CardTitle>
            </CardHeader>
            {/* شارة التسجيل */}
            <div className="flex items-center justify-between px-6">
              {savedOpeningBase != null && savedOpeningAir != null && (
                <Badge className="bg-violet-100 text-violet-700 border border-violet-200">
                  تم التسجيل: أساسي {formatNumber(savedOpeningBase)} | هواء {formatNumber(savedOpeningAir)} | درج {formatNumber(savedDrawerCash ?? 0)}
                </Badge>
              )}
            </div>
            <CardContent className="space-y-4">
              {/* Machine Selection Dropdown */}
              <div className="space-y-1">
                <label className="text-sm font-medium block text-right">اختيار الماكينة</label>
                <Select value={selectedMachineId || ''} onValueChange={setSelectedMachineId}>
                  <SelectTrigger className="w-full text-right flex-row-reverse" dir="rtl">
                    <SelectValue placeholder="اختر الماكينة" />
                  </SelectTrigger>
                  <SelectContent dir="rtl">
                    {machines.map((m) => (
                      <SelectItem key={m.id} value={m.id} className="text-right cursor-pointer justify-end">
                        {m.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-1">
                  <label className="text-sm font-medium block text-right">الرصيد الأساسي</label>
                  <Input
                    inputMode="decimal"
                    type="number"
                    min={0}
                    step="0.01"
                    value={openingBase}
                    onChange={(e) => setOpeningBase(e.target.value)}
                    placeholder="0.00"
                    className="text-right"
                  />
                </div>
                <div className="space-y-1">
                  <label className="text-sm font-medium block text-right">رصيد الهواء</label>
                  <Input
                    inputMode="decimal"
                    type="number"
                    min={0}
                    step="0.01"
                    value={openingAir}
                    onChange={(e) => setOpeningAir(e.target.value)}
                    placeholder="0.00"
                    className="text-right"
                  />
                </div>
                <div className="space-y-1">
                  <label className="text-sm font-medium block text-right">فلوس الدرج</label>
                  <Input
                    inputMode="decimal"
                    type="number"
                    min={0}
                    step="0.01"
                    value={drawerCash}
                    onChange={(e) => setDrawerCash(e.target.value)}
                    placeholder="0.00"
                    className="text-right"
                  />
                </div>
              </div>
              <div className="flex items-center justify-end">
                <Button variant="outline" className="mr-2" onClick={handleResetOpening}>
                  تصفير
                </Button>
                <Button onClick={handleAddOpening} className="bg-violet-600 hover:bg-violet-700">
                  إضافة
                </Button>
              </div>
            </CardContent>
          </Card>
          )}
          {activeSection === 'transfer' && (
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="flex items-center justify-between">
                <span className="text-right">التحويل</span>
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <div>
                <label className="text-sm font-medium block text-right">سعر الجملة</label>
                <Input
                  inputMode="decimal"
                  type="number"
                  min={0}
                  step="0.01"
                  value={wholesalePrice}
                  onChange={(e) => setWholesalePrice(e.target.value)}
                  placeholder="0.00"
                  className="text-right"
                />
              </div>
              <div>
                <label className="text-sm font-medium block text-right">سعر التجزئة</label>
                <Input
                  inputMode="decimal"
                  type="number"
                  min={0}
                  step="0.01"
                  value={retailPrice}
                  onChange={(e) => setRetailPrice(e.target.value)}
                  placeholder="0.00"
                  className="text-right"
                />
              </div>
              <div className="flex items-center justify-between">
                <Badge className="bg-green-100 text-green-700 border border-green-200">
                  الربح الفوري: {formatNumber((parseFloat(retailPrice || '0') - parseFloat(wholesalePrice || '0')) || 0)}
                </Badge>
                <Button onClick={handleTransferConfirm} disabled={savedOpeningBase == null || savedOpeningAir == null} className="bg-violet-600 hover:bg-violet-700">
                  تحويل
                </Button>
              </div>
            </CardContent>
          </Card>
          )}
          {activeSection === 'transferReceipts' && (
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="flex items-center justify-between">
                <div className="flex items-center gap-3">
                  <span className="text-right">إيصالات التحويل</span>
                  <Badge className="bg-green-100 text-green-700 border border-green-200">ربح: {formatNumber(totalTransferProfit)}</Badge>
                </div>
                <div className="flex items-center gap-2">
                  <Button variant="outline" size="sm" onClick={handleClearTransferReceipts} className="border-red-300 text-red-700 hover:bg-red-50">حذف الإيصالات</Button>
                </div>
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              {transferReceipts.length === 0 ? (
                <p className="text-sm text-gray-500 text-right">لا توجد تحويلات مسجلة.</p>
              ) : (
                <div className="space-y-2 max-h-64 overflow-y-auto pr-1">
                  {transferReceipts.map((t) => (
                    <div key={t.id} className="flex items-center justify-between p-3 rounded-lg border border-gray-100 bg-white dark:bg-gray-800 dark:border-gray-700 shadow-sm hover:shadow-md transition-shadow">
                      {/* Right: Prices */}
                      <div className="flex items-center gap-4 sm:gap-6">
                        <div className="text-center min-w-[3rem]">
                          <span className="block text-[10px] text-gray-500 mb-0.5">جملة</span>
                          <span className="block text-sm font-bold text-gray-900 dark:text-gray-100">{formatNumber(t.wholesalePrice)}</span>
                        </div>
                        <div className="h-8 w-px bg-gray-100 dark:bg-gray-700"></div>
                        <div className="text-center min-w-[3rem]">
                          <span className="block text-[10px] text-gray-500 mb-0.5">تجزئة</span>
                          <span className="block text-sm font-bold text-gray-900 dark:text-gray-100">{formatNumber(t.retailPrice)}</span>
                        </div>
                        <div className="h-8 w-px bg-gray-100 dark:bg-gray-700"></div>
                        <div className="text-center min-w-[3rem]">
                          <span className="block text-[10px] text-gray-500 mb-0.5">ربح</span>
                          <span className="block text-sm font-bold text-green-600">{formatNumber(t.profit)}</span>
                        </div>
                      </div>

                      <div className="flex items-center gap-3">
                        <Button
                          variant="ghost"
                          size="icon"
                          className="h-7 w-7 text-red-400 hover:text-red-600 hover:bg-red-50"
                          onClick={() => handleDeleteTransferReceipt(t)}
                          title="حذف واسترجاع الرصيد"
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>

                        {/* Left: Info */}
                        <div className="flex flex-col items-end gap-1.5">
                          {t.deductFrom && (
                            <span className={`text-[10px] px-2 py-0.5 rounded-full border ${
                              t.deductFrom === 'base' 
                                ? 'bg-blue-50 text-blue-700 border-blue-100' 
                               : 'bg-amber-50 text-amber-700 border-amber-100'
                            }`}>
                              {t.deductFrom === 'base' ? 'من الأساسي' : 'من الهواء'}
                            </span>
                          )}
                          <div className="flex items-center gap-2 text-[10px] text-gray-400">
                            <span>{formatDateTime(t.createdAt)}</span>
                            {t.cashierName && (
                              <>
                                <Separator orientation="vertical" className="h-3" />
                                <span title={t.cashierName}>{t.cashierName.split(' ')[0]}</span>
                              </>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
          )}
          {activeSection === 'cashWithdrawal' && (
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-right">سحب كاش</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {/* احصائيات السحب */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                 <div className="bg-blue-50 p-3 rounded border border-blue-100 flex flex-col justify-between">
                    <div className="space-y-1">
                        <span className="text-xs text-blue-600 block">سحب اليوم (يصفر 12 ليلاً)</span>
                        <span className="text-lg font-bold text-blue-800">{formatNumber(dailyWithdrawalsUsed)}</span>
                    </div>
                    <div className="pt-2 mt-2 border-t border-blue-200 flex justify-between items-center">
                        <span className="text-[10px] text-blue-500 font-medium">الربح اليومي</span>
                        <span className="text-sm font-bold text-blue-700">{formatNumber(dailyWithdrawalsProfit)}</span>
                    </div>
                 </div>
                 <div className="bg-green-50 p-3 rounded border border-green-100 flex flex-col justify-between">
                    <div className="space-y-1">
                        <span className="text-xs text-green-600 block">سحب الشهر (يصفر أول الشهر)</span>
                        <span className="text-lg font-bold text-green-800">{formatNumber(monthlyWithdrawalsUsed)}</span>
                    </div>
                    <div className="pt-2 mt-2 border-t border-green-200 flex justify-between items-center">
                        <span className="text-[10px] text-green-500 font-medium">الربح الشهري</span>
                        <span className="text-sm font-bold text-green-700">{formatNumber(monthlyWithdrawalsProfit)}</span>
                    </div>
                 </div>
              </div>

              <div className="grid grid-cols-1 gap-4">
                <div className="space-y-1">
                  <label className="text-sm font-medium block text-right">مبلغ السحب</label>
                  <Input
                    type="number"
                    min={0}
                    value={withdrawalAmount}
                    onChange={(e) => setWithdrawalAmount(e.target.value)}
                    placeholder="0.00"
                    className="text-right"
                  />
                </div>
                
                <div className="space-y-2 border p-3 rounded bg-gray-50">
                    <label className="text-sm font-medium block text-right mb-2">طريقة حساب العمولة</label>
                    <div className="flex justify-end gap-2 mb-3">
                         <Button 
                            variant={commissionMode === 'fixed' ? 'default' : 'outline'} 
                            size="sm"
                            onClick={() => setCommissionMode('fixed')}
                            className={commissionMode === 'fixed' ? 'bg-violet-600' : ''}
                         >
                            مبلغ ثابت
                         </Button>
                         <Button 
                            variant={commissionMode === 'rate' ? 'default' : 'outline'} 
                            size="sm"
                            onClick={() => setCommissionMode('rate')}
                            className={commissionMode === 'rate' ? 'bg-violet-600' : ''}
                         >
                            نسبة لكل ألف
                         </Button>
                    </div>

                    {commissionMode === 'rate' ? (
                        <div className="space-y-1">
                          <label className="text-sm font-medium block text-right">العمولة (لكل ألف)</label>
                          <Input
                            type="number"
                            min={0}
                            value={commissionRate}
                            onChange={(e) => setCommissionRate(e.target.value)}
                            placeholder="2"
                            className="text-right"
                          />
                        </div>
                    ) : (
                        <div className="space-y-1">
                          <label className="text-sm font-medium block text-right">مبلغ العمولة الكلي</label>
                          <Input
                            type="number"
                            min={0}
                            value={fixedCommissionAmount}
                            onChange={(e) => setFixedCommissionAmount(e.target.value)}
                            placeholder="0.00"
                            className="text-right"
                          />
                        </div>
                    )}
                </div>

                <div className="flex items-center space-x-2 space-x-reverse mt-2 justify-end">
                    <label htmlFor="deductDrawer" className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70 cursor-pointer">
                        خصم من درج النقدية
                    </label>
                    <Checkbox 
                        id="deductDrawer" 
                        checked={deductFromDrawer}
                        onCheckedChange={(c) => setDeductFromDrawer(!!c)} 
                    />
                </div>
              </div>

              <div className="flex items-center justify-between bg-gray-50 p-3 rounded-lg">
                <div className="text-sm text-gray-600">
                  سيتم تسجيل عمولة: <span className="font-bold text-green-600">
                    {formatNumber(
                        commissionMode === 'rate' 
                        ? (parseFloat(withdrawalAmount || '0') / 1000) * parseFloat(commissionRate || '0')
                        : parseFloat(fixedCommissionAmount || '0')
                    )}
                  </span>
                </div>
                <Button onClick={handleCashWithdrawal} disabled={isProcessing} className="bg-violet-600 hover:bg-violet-700 disabled:opacity-70">
                  {isProcessing ? 'جاري السحب...' : 'سحب'}
                </Button>
              </div>
            </CardContent>
          </Card>
          )}
          {activeSection === 'withdrawalReceipts' && (
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="flex items-center justify-between">
                <span className="text-right">إيصالات سحب مكنة</span>
                <Badge variant="outline" className="text-violet-700 border-violet-200 bg-violet-50">
                   عدد الإيصالات: {withdrawalReceipts.length}
                </Badge>
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              {withdrawalReceipts.length === 0 ? (
                <p className="text-sm text-gray-500 text-right">لا توجد سحوبات مسجلة لهذا الشهر.</p>
              ) : (
                <div className="space-y-2 max-h-[60vh] overflow-y-auto pr-1">
                  {withdrawalReceipts.map((w) => (
                    <div key={w.id} className="flex items-center justify-between p-3 rounded-lg border border-gray-100 bg-white dark:bg-gray-800 dark:border-gray-700 shadow-sm hover:shadow-md transition-shadow">
                      <div className="flex items-center gap-4">
                         <div className="text-right min-w-[4rem]">
                            <span className="block text-[10px] text-gray-500 mb-0.5">المبلغ</span>
                            <span className="block text-sm font-bold text-gray-900 dark:text-gray-100">{formatNumber(w.amount)}</span>
                         </div>
                         <div className="h-8 w-px bg-gray-100 dark:bg-gray-700"></div>
                         <div className="text-right min-w-[3rem]">
                            <span className="block text-[10px] text-gray-500 mb-0.5">العمولة</span>
                            <span className="block text-sm font-bold text-green-600">{formatNumber(w.commission)}</span>
                         </div>
                      </div>

                      <div className="flex items-center gap-3">
                        <Button
                          variant="ghost"
                          size="icon"
                          className="h-7 w-7 text-red-400 hover:text-red-600 hover:bg-red-50"
                          onClick={() => handleDeleteWithdrawalReceipt(w.id)}
                          title="حذف الإيصال"
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>

                        <div className="flex flex-col items-end gap-1.5">
                          <div className="flex items-center gap-2 text-[10px] text-gray-400">
                            <span>{formatDateTime(w.createdAt?.toISOString ? w.createdAt.toISOString() : w.createdAt)}</span>
                            {w.cashierName && (
                              <>
                                <Separator orientation="vertical" className="h-3" />
                                <span title={w.cashierName}>{w.cashierName.split(' ')[0]}</span>
                              </>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
          )}
          {activeSection === 'delivery' && (
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-right">تسليم الوردية</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-1">
                  <label className="text-sm font-medium block text-right">الرصيد الأساسي الحالي</label>
                  <Input
                    inputMode="decimal"
                    type="number"
                    min={0}
                    step="0.01"
                    value={deliveryBase}
                    onChange={(e) => setDeliveryBase(e.target.value)}
                    placeholder="0.00"
                    className="text-right"
                  />
                </div>
                <div className="space-y-1">
                  <label className="text-sm font-medium block text-right">رصيد الهواء الحالي</label>
                  <Input
                    inputMode="decimal"
                    type="number"
                    min={0}
                    step="0.01"
                    value={deliveryAir}
                    onChange={(e) => setDeliveryAir(e.target.value)}
                    placeholder="0.00"
                    className="text-right"
                  />
                </div>
              </div>
              <div className="flex items-center justify-end gap-2">
                <Button onClick={handleDoDelivery} className="bg-violet-600 hover:bg-violet-700">تسليم</Button>
              </div>

              {netResult != null && (
                <div className="mt-3">
                  <div className="text-right">
                    <Badge className={netResult >= 0 ? 'bg-green-100 text-green-700 border border-green-200' : 'bg-red-100 text-red-700 border border-red-200'}>
                      الناتج النهائي: {formatNumber(netResult)}
                    </Badge>
                  </div>
                  <div className="mt-2 flex items-center justify-end gap-2">
                    <Badge className="bg-blue-100 text-blue-700 border border-blue-200">
                      المجموع النهائي: {formatNumber(parseFloat(deliveryBase || '0') + parseFloat(deliveryAir || '0'))}
                    </Badge>
                    <Badge className="bg-amber-100 text-amber-700 border border-amber-200">
                      مطلوب من الدرج (بدون أرباح): {formatNumber(totalWholesaleTransferred)}
                    </Badge>
                  </div>
                </div>
              )}
            </CardContent>
          </Card>
          )}
          {activeSection === 'deliveryReceipts' && (
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="flex items-center justify-between">
                <span className="text-right">إيصالات التسليم</span>
                <div className="flex items-center gap-2">
                  <Button variant="outline" size="sm" onClick={handleClearMachineReceipts} className="border-red-300 text-red-700 hover:bg-red-50">حذف الإيصالات</Button>
                </div>
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              {receipts.length === 0 ? (
                <p className="text-sm text-gray-500 text-right">لا توجد إيصالات حتى الآن.</p>
              ) : (
                <div className="space-y-3">
                  {receipts.map((r) => (
                    <div key={r.id} className="rounded-lg border p-3 grid grid-cols-1 md:grid-cols-3 gap-3">
                      <div className="text-right space-y-1">
                        <p className="text-xs text-gray-500">الافتتاحي</p>
                        <p className="text-sm">أساسي: {formatNumber(r.openingBase)}</p>
                        <p className="text-sm">هواء: {formatNumber(r.openingAir)}</p>
                      </div>
                      <div className="text-right space-y-1">
                        <p className="text-xs text-gray-500">التسليم</p>
                        <p className="text-sm">أساسي: {formatNumber(r.deliveryBase)}</p>
                        <p className="text-sm">هواء: {formatNumber(r.deliveryAir)}</p>
                      </div>
                      <div className="text-right space-y-1">
                        <div className="flex items-center justify-end">
                          <Button variant="outline" size="sm" onClick={() => handleDeleteMachineReceipt(r.id)} className="border-red-300 text-red-700 hover:bg-red-50">
                            <Trash2 className="h-3 w-3 mr-1" /> حذف
                          </Button>
                        </div>
                        <p className="text-xs text-gray-500">النتيجة</p>
                        <p className={`text-sm ${r.netResult >= 0 ? 'text-green-700' : 'text-red-700'}`}>{formatNumber(r.netResult)}</p>
                        <div className="mt-1 flex items-center justify-end gap-2">
                          <span className="text-[11px] bg-blue-100 text-blue-700 border border-blue-200 rounded px-2 py-0.5">
                            المجموع النهائي: {formatNumber(r.deliveryBase + r.deliveryAir)}
                          </span>
                          {typeof r.requiredDrawerNoProfit === 'number' && (
                            <span className="text-[11px] bg-amber-100 text-amber-700 border border-amber-200 rounded px-2 py-0.5">
                              مطلوب من الدرج (بدون أرباح): {formatNumber(r.requiredDrawerNoProfit || 0)}
                            </span>
                          )}
                        </div>
                        <div className="flex items-center justify-end gap-2 text-xs text-gray-500">
                          <Calendar className="h-3 w-3" />
                          <span>{formatDateTime(r.createdAt)}</span>
                          {r.cashierName && (
                            <>
                              <Separator orientation="vertical" className="h-3" />
                              <span>الكاشير: {r.cashierName}</span>
                            </>
                          )}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
          )}
        </div>

        <DialogFooter className="sticky bottom-0 bg-white dark:bg-[#0F0F0F] z-20 border-t border-gray-200 dark:border-[#333] px-4 py-3 flex items-center justify-between">
          <Button variant="outline" onClick={() => handleClose(false)}>إغلاق</Button>
          <div className="text-xs text-gray-500 flex items-center gap-1">
            <Clock className="h-3 w-3" />
            <span>يسجل التاريخ والوقت تلقائياً لكل إيصال</span>
          </div>
        </DialogFooter>
      </DialogContent>

      {/* نافذة تحويل الرصيد */}
      <Dialog open={transferDialogOpen} onOpenChange={setTransferDialogOpen}>
        <DialogContent className="sm:max-w-sm">
          <DialogHeader>
            <DialogTitle>تحويل رصيد</DialogTitle>
          </DialogHeader>
          <div className="space-y-3">
            <div>
              <label className="text-sm font-medium block text-right">سعر الجملة</label>
              <Input
                inputMode="decimal"
                type="number"
                min={0}
                step="0.01"
                value={wholesalePrice}
                onChange={(e) => setWholesalePrice(e.target.value)}
                placeholder="0.00"
                className="text-right"
              />
            </div>
            <div>
              <label className="text-sm font-medium block text-right">سعر التجزئة</label>
              <Input
                inputMode="decimal"
                type="number"
                min={0}
                step="0.01"
                value={retailPrice}
                onChange={(e) => setRetailPrice(e.target.value)}
                placeholder="0.00"
                className="text-right"
              />
            </div>
            <div className="text-right">
              <Badge className="bg-green-100 text-green-700 border border-green-200">
                الربح الفوري: {formatNumber((parseFloat(retailPrice || '0') - parseFloat(wholesalePrice || '0')) || 0)}
              </Badge>
            </div>
          </div>
          <DialogFooter className="flex items-center justify-end gap-2">
            <Button variant="secondary" onClick={() => setTransferDialogOpen(false)}>إلغاء</Button>
            <Button onClick={handleTransferConfirm} className="bg-violet-600 hover:bg-violet-700">تحويل</Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
      {/* نافذة اختيار مصدر الخصم */}
      <AlertDialog open={deductDialogOpen} onOpenChange={setDeductDialogOpen}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>اختيار مصدر الخصم</AlertDialogTitle>
            <AlertDialogDescription>هل يتم الخصم من الرصيد الأساسي أم من رصيد الهواء؟</AlertDialogDescription>
          </AlertDialogHeader>
          <div className="flex items-center justify-end gap-2 mt-4">
            <Button variant="ghost" onClick={() => setDeductDialogOpen(false)} disabled={isProcessing}>رجوع</Button>
            <Button 
              className="bg-emerald-600 hover:bg-emerald-700 text-white" 
              onClick={() => finalizeTransfer('base')}
              disabled={isProcessing}
            >
              {isProcessing ? 'جاري التحويل...' : 'خصم من الأساسي'}
            </Button>
            <Button 
              className="bg-blue-600 hover:bg-blue-700 text-white" 
              onClick={() => finalizeTransfer('air')}
              disabled={isProcessing}
            >
              {isProcessing ? 'جاري التحويل...' : 'خصم من الهواء'}
            </Button>
          </div>
        </AlertDialogContent>
      </AlertDialog>
      
      <VideoModal
        isOpen={isVideoModalOpen}
        onClose={() => setIsVideoModalOpen(false)}
        videoUrl="https://www.youtube.com/watch?v=sHWKmqAmxic"
        title="شرح قسم مكن الشحن وتسجيل اليومية"
      />
    </Dialog>
  );
}
